<template>
  <div class="row" style="padding-top: 50px; padding-bottom: 50px;">
    <div class="col-md-10 col-md-offset-1">
      <h3 class="mr-b-20 text-center">Personal Details</h3>
      <hr>
      <form @submit.prevent="changeState">
        <div class="form-group">
          <label for="" class="control-label">Full Name</label>
          <input type="text" class="form-control" id="" placeholder="Enter your full name" v-model="name" required>
        </div>
        <div class="form-group">
          <label for="" class="control-label">Email</label>
          <input type="email" class="form-control" id="" placeholder="Enter your email" v-model="email" required>
        </div>
        <hr>
        <div class="form-group mr-t-20 text-center">
          <button type="submit" class="btn btn-lg btn-primary">Proceed</button>
          <!--<button type="submit" class="btn btn-lg btn-primary" v-else disabled><i class='fa fa-spinner fa-spin'></i> Loading Menu Items</button>-->
        </div>
      </form>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['mealitems', 'token'],
    data() {
      return {
        name: '',
        email: ''
      }
    },
    methods: {
      changeState() {
        this.$emit('changeState', 1, {name: this.name, email: this.email})
      },
      // async fetchData() {
      //   const res = await Devless.addData('mkoo', 'meal_query', { token: this.token })
      //   console.log(res)
      //   if (res.status_code === 625) {
      //     this.meals = _.groupBy(res.payload, 'day');
      //     // console.log(this.meals)
      //     return
      //   }
      // }
    }
  }
</script>


