          <div class="form-group col-xs-6">
            <select class="form-control" v-model="thursday.lunch.data" name="thu_lunch" v-validate data-vv-rules="required">
              <option disabled value="">-- Select Lunch --</option>
              <option v-for="m in meals.thursday" v-if="m.type === 'lunch'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
            <span v-show="errors.has('thu_lunch')" class="help is-danger">This field is required.</span>
