          <div class="form-group col-xs-6">
            <select class="form-control" v-model="thursday.dinner.data" name="thu_dinner" v-validate data-vv-rules="required">
              <option disabled value="">-- Select Dinner --</option>
              <option v-for="m in meals.thursday" v-if="m.type === 'dinner'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
