        </div>
        <div class="row">
          <div class="col-md-12">
            <h4>Saturday</h4>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" v-model="saturday.lunch.data" name="sat_lunch" v-validate data-vv-rules="required">
              <option disabled value="">-- Select Lunch --</option>
              <option v-for="m in meals.saturday" v-if="m.type === 'lunch'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
            <span v-show="errors.has('sat_lunch')" class="help is-danger">This field is required.</span>
          </div>
          <div class="form-group col-xs-6">
            <select class="form-control" v-model="saturday.dinner.data" name="sat_dinner" v-validate data-vv-rules="required">
              <option disabled value="">-- Select Dinner --</option>
              <option v-for="m in meals.saturday" v-if="m.type === 'dinner'" :value="m.mkoo_meal_id">
                {{ m.related.meal[0].name }}
              </option>
            </select>
            <span v-show="errors.has('sat_dinner')" class="help is-danger">This field is required.</span>
          </div>
        </div>
        <div class="row" v-show="meals.length !== 0">
          <div class="form-group mr-t-20 col-md-12 text-center">
            <button @click="validateBeforeSubmit(0)" v-if="editMode" class="btn btn-primary">Submit Order</button>
            <button @click="validateBeforeSubmit(1)" v-else class="btn btn-primary">Update Order</button>
          </div>
        </div>
        <!--</div>
              </div>-->
      </form>
    </div>
    <div class="row loading" v-show="loading">Loading&#8230;</div>
  </div>

</template>

<script>
  import Devless from './utils/devless'
  import _ from 'lodash'

  export default {
    props: ['token', 'details', 'mealitems'],
    data() {
      return {
        editMode: true,
        loading: false,
        monday: {
          lunch: {
            id: '',
            data: ''
          },
          dinner: {
            id: '',
            data: ''
          }
        },
        tuesday: {
          lunch: {
            id: '',
            data: ''
          },
          dinner: {
            id: '',
            data: ''
          }
        },
        wednesday: {
          lunch: {
            id: '',
            data: ''
          },
          dinner: {
            id: '',
            data: ''
          }
        },
        thursday: {
          lunch: {
            id: '',
            data: ''
          },
          dinner: {
            id: '',
            data: ''
          }
        },
        friday: {
          lunch: {
            id: '',
            data: ''
          },
          dinner: {
            id: '',
            data: ''
          }
        },
        saturday: {
          lunch: {
            id: '',
            data: ''
          },
          dinner: {
            id: '',
            data: ''
          }
        },
        meals: []
      }
    },
    methods: {
      async fetchData() {
        // this.token = window.location.search.substr(9)
          if(this.token === '') {
            alert ('Oops... Invalid URL')
            return
          }
          const res = await Devless.addData('mkoo', 'meal_query', { token: this.token, email: this.details.email })
          if (res.status_code === 625) {
            if(res.payload.related.old_choice.length !== 0) {
              const selected_items = _.groupBy(res.payload.related.old_choice, 'day');
              _.forEach(selected_items, function(val, key) {
                selected_items[key] = _.groupBy(selected_items[key], 'type');
              })

              for(var key in selected_items) {
                if(selected_items.hasOwnProperty(key)) {
                  for(var val in selected_items[key]) {
                    if(selected_items[key].hasOwnProperty(val)) {
                      this.$data[key][val].data = selected_items[key][val][0].mkoo_meal_id;
                      this.$data[key][val].id = selected_items[key][val][0].id;
                    }
                  }
                }
              }

              this.editMode = !this.editMode
            }

            this.meals = _.groupBy(res.payload, 'day');
            this.loading = false;
            return
          } else {
            alert('Error fetching meals')
          }
      },
      async addData(method) {
        let orders = {
          "link": this.token,
          "email": this.details.email,
          "name": this.details.name,
          "ordered_meals": [
            {
              "id": this.monday.lunch.id,
              "day": "monday",
              "type": "lunch",
              "mkoo_meal_id": this.monday.lunch.data
            },
            {
              "id": this.monday.dinner.id,
              "day": "monday",
              "type": "dinner",
              "mkoo_meal_id": this.monday.dinner.data
            },
            {
              "id": this.tuesday.lunch.id,
              "day": "tuesday",
              "type": "lunch",
              "mkoo_meal_id": this.tuesday.lunch.data
            },
            {
              "id": this.tuesday.dinner.id,
              "day": "tuesday",
              "type": "dinner",
              "mkoo_meal_id": this.tuesday.dinner.data
            },
            {
              "id": this.wednesday.lunch.id,
              "day": "wednesday",
              "type": "lunch",
              "mkoo_meal_id": this.wednesday.lunch.data
            },
            {
              "id": this.wednesday.dinner.id,
              "day": "wednesday",
              "type": "dinner",
              "mkoo_meal_id": this.wednesday.dinner.data
            },
            {
              "id": this.thursday.lunch.id,
              "day": "thursday",
              "type": "lunch",
              "mkoo_meal_id": this.thursday.lunch.data
            },
            {
              "id": this.thursday.dinner.id,
              "day": "thursday",
              "type": "dinner",
              "mkoo_meal_id": this.thursday.dinner.data
            },
            {
              "id": this.friday.lunch.id,
              "day": "friday",
              "type": "lunch",
              "mkoo_meal_id": this.friday.lunch.data
            },
            {
              "id": this.friday.dinner.id,
              "day": "friday",
              "type": "dinner",
              "mkoo_meal_id": this.friday.dinner.data
            },
            {
              "id": this.saturday.lunch.id,
              "day": "saturday",
              "type": "lunch",
              "mkoo_meal_id": this.saturday.lunch.data
            },
            {
              "id": this.saturday.dinner.id,
              "day": "saturday",
              "type": "dinner",
              "mkoo_meal_id": this.saturday.dinner.data
            }
          ]
        };
        
        $('button').button('loading')
        let res = undefined;
        if(method === 0) {
          _.forEach(orders.ordered_meals, function(val, key) {
            delete orders.ordered_meals[key]['id']
          })
          
          res = await Devless.addData('mkoo', 'place_order', orders)
        } else {
          res = await Devless.addData('mkoo', 'update_order', orders)
        }

        if (res.status_code === 609) {
          $('button').button('reset')
          this.$emit('resetState', 2)
        } else {
          alert(res.message)
        }
        
      },
      validateBeforeSubmit(method) {
        this.$validator.validateAll().then((result) => {
          if (result) {
            // eslint-disable-next-line
            this.addData(method)
            return;
          }
          alert('Correct the fields highlighted in red!');
        });
      }
    },
    mounted() {
      this.loading = true;
      this.fetchData();
    }
  }
</script>

<style scoped>
/* Absolute Center Spinner */
.loading {
  position: fixed;
  z-index: 999;
  height: 2em;
  width: 2em;
  overflow: show;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

/* Transparent Overlay */
.loading:before {
  content: '';
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.3);
}

/* :not(:required) hides these rules from IE9 and below */
.loading:not(:required) {
  /* hide "loading..." text */
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}

.loading:not(:required):after {
  content: '';
  display: block;
  font-size: 10px;
  width: 1em;
  height: 1em;
  margin-top: -0.5em;
  -webkit-animation: spinner 1500ms infinite linear;
  -moz-animation: spinner 1500ms infinite linear;
  -ms-animation: spinner 1500ms infinite linear;
  -o-animation: spinner 1500ms infinite linear;
  animation: spinner 1500ms infinite linear;
  border-radius: 0.5em;
  -webkit-box-shadow: rgba(255, 255, 255, 1) 1.5em 0 0 0, rgba(255, 255, 255, 1) 1.1em 1.1em 0 0, rgba(255, 255, 255, 1) 0 1.5em 0 0, rgba(255, 255, 255, 1) -1.1em 1.1em 0 0, rgba(0, 0, 0, 0.5) -1.5em 0 0 0, rgba(0, 0, 0, 0.5) -1.1em -1.1em 0 0, rgba(255, 255, 255, 1) 0 -1.5em 0 0, rgba(255, 255, 255, 1) 1.1em -1.1em 0 0;
  box-shadow: rgba(255, 255, 255, 1) 1.5em 0 0 0, rgba(255, 255, 255, 1) 1.1em 1.1em 0 0, rgba(255, 255, 255, 1) 0 1.5em 0 0, rgba(255, 255, 255, 1) -1.1em 1.1em 0 0, rgba(255, 255, 255, 1) -1.5em 0 0 0, rgba(255, 255, 255, 1) -1.1em -1.1em 0 0, rgba(255, 255, 255, 1) 0 -1.5em 0 0, rgba(255, 255, 255, 1) 1.1em -1.1em 0 0;
}

/* Animation */

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-moz-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-o-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.is-danger {
  color: red
}
</style>


