<template>
  <div class="row text-center dead-center">
			<div class="pd-tb-mini mr-b-30">
				<img src="./assets/success.png">
				<h2>Thank You.</h2>
				<p style="font-size:16px; color:#5C5C5C; margin-top: 20px">You have successfully submitted your meal for the week.</p>
				<a @click="resetState" class="btn btn-success mr-t-20">Back to Home Page</a>
			</div>
	</div>
</template>

<script>
  export default {
    methods: {
      resetState() {
        this.$emit('resetState', 0)
      }
    }
  }
</script>


