<template>
  <div id="app">
    <div class="container-fluid">
      <div class="row">
        <div id="leftPage" class="leftPage col-md-7 pd-tb-mini pd-r-50">
          <h1 class="text-white text-center">Mkoo</h1>
        </div>
        <div style="">
          <div id="rightPage" class="col-lg-5">
					  <div class="rightContent">
              <transition-group name="slide-fade">
                <order-view :key="0" :token="token" v-if="state === 1"  v-on:changeState="changeState" :mealitems="meals" :details="details" v-on:resetState="resetState"></order-view>
                <details-view :key="1" :token="token" v-show="state === 0" v-on:changeState="changeState" :mealitems="meals"></details-view>
                <thank-you :key="2" v-show="state === 2" v-on:resetState="resetState"></thank-you>
              </transition-group>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import OrderView from './OrderView';
  import DetailsView from './DetailsView';
  import ThankYou from './ThankYou';
  import Devless from './utils/devless';
  import _ from 'lodash'

  export default {
    name: 'app',
    components: {
      OrderView,
      DetailsView,
      ThankYou
    },
    data() {
      return {
        meals: undefined,
        state: 0,
        details: undefined,
        token: undefined
      }
    },
    methods: {
      changeState(state, obj) {
        this.state = state;
        this.details = obj;
      },
      async fetchData() {
        const res = await Devless.addData('mkoo', 'meal_query', { token: this.token })
        console.log(res)
        if (res.status_code === 625) {
          this.meals = _.groupBy(res.payload, 'day');
          // console.log(this.meals)
          return
        } else {
          alert('Incorrect link')
        }
      },
      resetState(state) {
        this.state = state
      }
    },
    created() {
      this.token = window.location.search.substr(9)
      // this.fetchData()
    }
  }
</script>

<style>
  .slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
</style>


